
import { computed, defineComponent, ref } from 'vue'
import { useStore } from '@/store'
import { useRouter, useRoute } from 'vue-router'

import { pathMapToMenu } from '@/utils/map-menus'

export default defineComponent({
  props: {
    collapse: {
      type: Boolean,
      default: false
    }
  },
  setup() {
    //store
    const store = useStore()
    const userMenus = computed(() => store.state.login.userMenus)

    //router
    const router = useRouter()
    const route = useRoute()
    const currentPath = route.path

    //data
    const menu = pathMapToMenu(userMenus.value, currentPath)
    const defaultValue = ref(menu.id + '')

    //event handle
    const handleMenuItemClick = (item: any) => {
      router.push({
        path: item.url ?? '/not-found'
      })
    }
    const menuIconName = (itemicon: string) => {
      //把item.icon的旧图标名称改成新图标名称，"el-icon-chat-line-round"=>"ChatLineRound"
      let str = ''
      itemicon
        .slice(8)
        .split('-')
        .forEach((item: string) => {
          str =
            str + item.slice(0, 1).toUpperCase() + item.slice(1).toLowerCase()
        })
      return str
    }

    return { userMenus, menuIconName, handleMenuItemClick, defaultValue }
  }
})
